.header {
    display: flex;
}

.header_body {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #f1eeef;
    width: 50%;
    font-size: 14px;
}

.header_body_nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #f1eeef;
    width: 50%;
    font-size: 14px;
}

.header_body_nav_item {
    display: flex;
    justify-self: end;
    cursor: pointer;
}

.header_body_item {
}

.header_body_nav_item:hover {
    text-decoration: underline;
}

.header_body_nav_image {
    width: 25px;
    padding: 0 10px 0 15px;
}

.header_body_age {
    display: flex;
    background-color: rgba(0,0,0,.05);
    border-radius: 2px;
    color: #999;
    font-size: 14px;
    padding: 2px;
    margin-right: 20px;
}

.gray {
    color: gray;
}