.footer {
    background-color: #3e4753;
    display: flex;
    padding: 22px 20px
}

.footer_text {
    display: flex;
    flex-wrap: wrap;
}

.footer_text_item {
    color: white;
    font-size: 13px;
    margin: 0 12px 9px 0;
    cursor: pointer;
    transition: .2s;
    line-height: 1.231;
}

.footer_text_item:hover {
    color: rgb(204, 204, 204);
    text-decoration: underline;
}

.footer_text_and_image {
    display: inline;
}

.footer_image {
    display: inline-block;
}

.footer_image_text {
    display: inline;
    margin-left: 20px;
    color: rgb(107, 115, 127);
    font-size: 13px;
}

.footer_image img {
    display: block;
    width: 65px;
    margin: 0 55px 7px;
}

.social_network_image {
    display: flex;
    margin: 15px 0 0;
    width: 30px;
    height: 30px;
}

.yellow {
    color: rgb(255, 218, 80);
}

.yellow:hover {
    color: rgb(255, 163, 53);
}

.item_image_footer {
    background-color: #28313c;
    margin-right: 10px;
    cursor: pointer;
    transition: .2s;
}

.item_image_footer:hover {
    background-color: black;
}

.social_network_image img {
    width: 30px;
    height: 30px;
}