.content {
}

.grid {
    display: inline-flex;
    flex-wrap: wrap;
    width: 1102px;
}

.content_item {
    background: #fff center no-repeat;
    transition: .15s;
    width: 326px;
    height: 119px;
    background-size: cover;
    cursor: pointer;
    padding: 20px 20px 10px;
    margin: 1px 1px 0 0;
}

.content_item:last-child {
    width: 327px;
}

.content_item:hover {
    opacity: 0.9;
}

.text_content {
    width: 225px;
    font-size: 15px;
    color: white;
    position: absolute;
    font-weight: bold;
    cursor: pointer;
}

.sub_title {
    font-size: 25px;
    font-weight: 400;
    color: #fff;
}

.advertising {
    display: inline-block;
}

.advertising img {
    position: absolute;
    top: 192px;
}